.poster-bg {
  width: 100%;
  height: 100%;
  background-image: url(/static/media/bg.f6c7206d.jpg);
  background-size: 100%;
  overflow: hidden;
}
.poster-bg-top {
  position: absolute;
  top: 0;
  right: 0;
  width: 22%;
  height: 26%;
  background-image: url(/static/media/top.04d139cf.png);
  background-size: 100%;
  background-repeat: no-repeat;
}
.poster-bg-mid {
  position: absolute;
  top: 56%;
  left: 0;
  width: 40px;
  height: 94px;
  background-image: url(data:image/png;base64,);
  background-size: 100%;
  background-repeat: no-repeat;
}
.poster-bg-bottom {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 64px;
  height: 63px;
  background-image: url(/static/media/bottom.cd189b99.png);
  background-size: 100%;
  background-repeat: no-repeat;
}
.poster-maintitle {
  position: absolute;
  top: 10%;
  right: 0;
  width: 95%;
  height: 40%;
  background-image: url(/static/media/title.60cf95a2.png);
  background-size: 100%;
  background-repeat: no-repeat;
}
.poster-title {
  position: absolute;
  top: 50%;
  padding: 22px 0;
  width: 76%;
  height: auto;
  background-image: url(data:image/png;base64,);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.poster-title .match-date {
  font-family: MiSans-Heavy;
  font-size: 20px;
  color: #103382;
  letter-spacing: 5px;
  font-weight: 900;
}
.poster-title .match-name {
  font-family: MiSans-Heavy;
  font-size: 16px;
  color: #103382;
  font-weight: 900;
}
.poster-subtitle {
  position: absolute;
  bottom: 15%;
  width: 280px;
  height: 68px;
  background-image: url(/static/media/divider.bba6cfc7.png);
  background-size: 100%;
  background-repeat: no-repeat;
}
