.detail-table {
  margin: 0 10px;
  width: 340px;
  height: 192px;
}
.detail-table-container-0 {
  border-radius: 5px;
  background: #FFFFFF;
  border: 2px solid #f2e6b8;
}
.detail-table-container-0 .name {
  font-family: PingFangSC-Semibold;
  font-size: 10px;
  color: #BE7610;
  font-weight: 600;
  height: 62.5px;
  width: 64px;
}
.detail-table-container-0 th,
.detail-table-container-0 td {
  font-weight: 400;
  border: 0.5px solid #ebe2d7;
}
.detail-table-container-1 {
  margin-top: 27px;
  border-radius: 5px;
  background: #FFFFFF;
  border: 2px solid #cddaeb;
}
.detail-table-container-1 .name {
  font-family: PingFangSC-Semibold;
  font-size: 10px;
  color: #1956B3;
  font-weight: 600;
  height: 62.5px;
  width: 62px;
}
.detail-table-container-1 th,
.detail-table-container-1 td {
  font-weight: 400;
  border: 0.5px solid #d7e1eb;
}
.detail-table .second-head {
  height: 36px;
  font-size: 9px;
}
.detail-table .highlight-0 {
  background: #F9F5EE;
  font-family: PingFangSC-Medium;
  font-size: 10px;
  color: #BE7610;
  font-weight: 500;
}
.detail-table .highlight-1 {
  background: #F2F6FA;
  font-family: PingFangSC-Medium;
  font-size: 10px;
  color: #0058B9;
  font-weight: 500;
}
.detail-table table {
  width: 336px;
  height: 192px;
  font-family: PingFangSC-Regular;
  font-size: 10px;
  color: #383F4B;
  font-weight: 400;
  border-collapse: collapse;
  border-style: hidden;
}
