.score-table {
  width: 340px;
  height: 145px;
  display: flex;
  justify-content: center;
  background: #FFFFFF;
  border: 2px solid #f2e6b8;
  border-radius: 5px;
}
.score-table table {
  width: 313px;
  height: 128px;
  color: #414141;
  font-size: 10px;
  border-collapse: collapse;
}
.score-table table,
.score-table tr,
.score-table th,
.score-table td {
  border-bottom: 0.5px solid #e4ddd8;
}
.score-table .row-head {
  text-align: left;
  padding-left: 5px;
}
.score-table .lose {
  color: #C7BFB6;
}
