.player-bg {
  position: relative;
  width: 340px;
  height: 156px;
}
.player-bg .flag-bg {
  width: 100%;
  height: 113px;
  border: 1px solid #ffffff;
}
.player-bg-container .match-name {
  position: absolute;
  top: 88px;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 340px;
  height: 25px;
  background: rgba(228, 156, 21, 0.5);
  border: 1px solid #ffffff;
  font-family: PingFangSC-Semibold;
  font-size: 12px;
  color: #FFFFFF;
  font-weight: 600;
}
.player-bg-container .match-name .icon {
  width: 35px;
  height: 22px;
  margin: 0 8px 0 12px;
  background-color: #000000;
  background-image: url(data:image/png;base64,);
  background-size: 55%;
  background-repeat: no-repeat;
  background-position: center;
}
.player-bg-container .bg-head-groups {
  position: absolute;
  top: 113px;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 9px;
  color: #FFFFFF;
}
.player-bg-container .bg-head-groups .bg-head {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  width: 170px;
  height: 21px;
  background: rgba(228, 156, 21, 0.5);
  border: 1px solid #ffffff;
}
.player-bg-container .bg-head-groups .bg-head-name {
  position: absolute;
  left: 12px;
}
.player-bg-container .bg-head-groups .bg-head-ranking {
  position: absolute;
  right: 3px;
}
.player-bg .bg-intro-groups {
  position: absolute;
  top: 134px;
  display: flex;
  flex-direction: row;
  font-size: 10px;
  color: #000000;
  background: #FFFFFF;
  border: 1px solid #ffffff;
  box-shadow: 0px 3px 4px 0px rgba(192, 192, 192, 0.5);
}
.player-bg .bg-intro-groups .bg-intro {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  width: 170px;
  height: 23px;
  background: rgba(255, 255, 255, 0.75);
  border: 1px solid #ffffff;
  text-align: center;
  line-height: 10px;
}
.player-bg .bg-intro-groups .bg-intro-country {
  position: absolute;
  left: 12px;
}
.player-bg .bg-intro-groups .bg-intro-flag {
  position: absolute;
  left: 36px;
  width: 15px;
  height: 10px;
  box-shadow: 0px 1px 1px 0px rgba(157, 157, 157, 0.5);
}
.player-bg .bg-intro-groups .bg-intro-name {
  position: absolute;
  left: 62px;
}
.player-bg .bg-intro-groups .bg-intro-ranking {
  position: absolute;
  right: 3px;
}
.player-score {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 340px;
  height: 122px;
  margin-top: 18px;
  padding: 0 31px;
  background: #FFFFFF;
  border: 2px solid #f2e6b8;
  border-radius: 5px;
}
.player-score .player-info {
  display: flex;
  flex-direction: column;
  width: 70px;
  height: 122px;
  font-family: PingFangSC-Regular;
  font-size: 10px;
  color: #000000;
}
.player-score .player-info-name {
  margin-top: 16px;
}
.player-score .player-info-icon {
  margin-top: 9px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  box-shadow: 0 3px 5px 0 rgba(174, 190, 200, 0.5);
  object-fit: cover;
}
.player-score .match-score {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 138px;
  height: 122px;
}
.player-score .match-score-vs {
  position: absolute;
  top: 14px;
  font-family: Arial-BoldMT;
  font-size: 10px;
  color: #000000;
  font-weight: 700;
}
.player-score .match-score-detail {
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 52px;
  font-family: Helvetica;
  font-size: 30px;
  color: #808080;
  font-weight: 400;
}
.player-score .match-score-detail .score-highlight {
  color: #000000;
}
